import React from 'react'
import Container from 'react-bootstrap/Container'
import "./Resume-style.scss"
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import { userInfo, ExperienceProps, EducationProps } from '../../constants/user'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUniversity } from '@fortawesome/free-solid-svg-icons'
import { colors } from '../../style'

interface Props {
    id: string
}


export default function Experience({ ...props }: Props) {

    const diff_years = (dt2: any, dt1: any) => {
        var diff = (dt2.getTime() - dt1.getTime()) / 1000;
        diff /= (60 * 60 * 24);
        return Math.abs(Math.round(diff / 365.25));
    }

    const diff_months = (dt2: any, dt1: any) => {
        var diff = (dt2.getTime() - dt1.getTime()) / 1000;
        diff /= (60 * 60 * 24 * 7 * 4);
        return Math.abs(Math.round(diff));
    }

    const RenderList = ({ startDate, endDate, companyName, possition, desc }: ExperienceProps) => {
        const MONTHS = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
        let monthInd = startDate.getUTCMonth() + 1
        let year = startDate.getFullYear()
        let monthOfExp = null;
        let yearOfExp = 0;
        let expString = ""
        if (endDate === "Present") {
            let noOfMonths = diff_months(new Date(), startDate)
            console.log(noOfMonths)
            let noOfYr = noOfMonths > 12 ? parseInt((noOfMonths / 12).toString()) : 0
            console.log(noOfYr)
            yearOfExp = noOfYr
            if (noOfMonths > 12) {
                monthOfExp = noOfMonths - (12 * noOfYr)
                expString = `${yearOfExp} yrs ${monthOfExp} mos`
            } else {
                monthOfExp = noOfMonths
                expString = `${monthOfExp - 1} mos`
            }
        }

        return (
            <div className="list-item-container" data-aos="fade-up">
                <div className="info-icon">
                    <div className="resume-circle">
                        <FontAwesomeIcon icon={faUniversity} size={"lg"} color={colors.primary_dark} />
                    </div>
                </div>
                <div className="info">
                    <p className="resume-subTitle" >{`${MONTHS[monthInd]} ${year} - ${endDate} â€¢ ${expString} `}</p>
                    <p className="resume-heading">{companyName}</p>
                    <p className="resume-subHeading">{possition}</p>
                    <p className="resume-desc">{desc}</p>
                </div>
            </div>
        )
    }

    return (
        <Row id={props.id}>
            <Col lg={12}>
                <br /><br /><br /><br />
                <p className="page-title" >Experience</p>
                {userInfo.resume.experience?.map((val, index) => <RenderList {...val} key={index} />)}
            </Col>
        </Row>
    )
}