import React, { useState } from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup'
import ToggleButton from 'react-bootstrap/ToggleButton'
import Button from 'react-bootstrap/Button'
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import Carousel from 'react-bootstrap/Carousel'
import { userInfo, Skills as SkillInfoProps } from '../../constants/user'
import "./Resume-style.scss"
import styled, { keyframes, } from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { colors } from '../../style'

interface Props {
    id?: string
}

const SkillCard = React.memo(({ domain, perc, delay }: any) => {
    return (
        <div className="card-container" data-aos="fade-up" data-aos-delay={delay} >
            <div className="card-heading">
                <p className="card-skill-title">{domain}</p>
            </div>
            <div className="card-progress-container">
                <svg height="100%" width="100%">
                    <circle cx="50%" cy="50%" r="70" />
                    <circle cx="50%" cy="50%" r="70" style={{ strokeDashoffset: 440 - (440 * perc) / 100 }} />
                </svg>
                <p className="card-skill-title percentage-circle">{perc + "%"}</p>
            </div>
        </div>
    )
})

const SkillInfo = React.memo(({ skillName, experience }: SkillInfoProps) => {

    const progress = keyframes`
        from 
            {
                width : 0%;
            };
        to{
            width: ${experience + "%"};
        };
    `;

    const ProgressDiv = styled.div`
        width: ${experience + "%"};
        animation: ${progress} 1s ease;
    `;

    return (
        <div className="skill-info">
            <p className="skillName">{skillName}</p>
            <ProgressDiv className="skill-bar" />
        </div>
    )
})

const SkillsConainer = React.memo(() => {
    const [activeIndex, setActiveIndex] = useState(0)
    const [skillNameRef, setRef] = useState<HTMLParagraphElement | null>(null);
    const [hrRef, setHrRef] = useState<HTMLHRElement | null>(null);
    const skillsArr = ['Front End', "Back End", "Mobile App Dev", "Other"]
    const { skills } = userInfo.resume

    const startAnim = () => {
        skillNameRef?.classList.remove("animate__fadeInRight")
        skillNameRef?.classList.toggle("animate__fadeInRight");
        skillNameRef?.addEventListener('animationend', () => {
            skillNameRef?.classList.remove("animate__fadeInRight")
        });
    }

    const handleNext = () => {
        setActiveIndex((activeIndex + 1) % 4)
        startAnim()
    }

    const handlePrev = () => {
        setActiveIndex((((activeIndex - 1) % 4) + 4) % 4)
    }

    return (
        <Col lg={12}>
            <div className="skill-box-container">
                <div className="skills-but-container">
                    <ToggleButtonGroup as="div" type="radio" name="options" defaultValue={activeIndex} className="skills-but-group" >
                        <ToggleButton id="togal-but" className="one" value={0} onClick={() => setActiveIndex(0)} >Front End</ToggleButton>
                        <ToggleButton id="togal-but" className="two" value={1} onClick={() => setActiveIndex(1)}>Back End</ToggleButton>
                        <ToggleButton id="togal-but" className="three" value={2} onClick={() => setActiveIndex(2)}>Mobile App Dev</ToggleButton>
                        <ToggleButton id="togal-but" className="four" value={3} onClick={() => setActiveIndex(3)}>Other</ToggleButton>
                        <hr ref={ref => { setHrRef(ref) }} />
                    </ToggleButtonGroup>
                </div>
                <div className="skill-box-container-sm">
                    <div className="active-skill">
                        <p className="skillName-title animate__animated" ref={ref => { setRef(ref) }}>{skillsArr[activeIndex]}</p>
                    </div>
                    <div>
                        <ButtonGroup className="project-toggle-group" >
                            <Button id="project-toggle-but1" aria-label="Prev" onClick={handlePrev}>
                                <FontAwesomeIcon size="lg" icon={faChevronLeft} color={colors.secondary} />
                            </Button>
                            <Button id="project-toggle-but2" aria-label="Naxt" onClick={handleNext}>
                                <FontAwesomeIcon size="lg" icon={faChevronRight} color={colors.primary} />
                            </Button>
                        </ButtonGroup>
                    </div>
                </div>
                <div>
                    <Carousel
                        activeIndex={activeIndex}
                        onSelect={(ind) => { setActiveIndex(ind); startAnim() }}
                        slide={false}
                        interval={null}
                        controls={false}
                        indicators={false}
                        fade={true}
                        touch={true}>
                        <Carousel.Item>
                            <div className="skill-box">
                                {skills?.frontEnd.map((val, index) => <SkillInfo skillName={val.skillName} experience={val.experience} />)}
                            </div>
                        </Carousel.Item>
                        <Carousel.Item>
                            <div className="skill-box">
                                {skills?.backEnd.map((val, index) => <SkillInfo skillName={val.skillName} experience={val.experience} />)}
                            </div>
                        </Carousel.Item>
                        <Carousel.Item>
                            <div className="skill-box">
                                {skills?.mobileAppDev.map((val, index) => <SkillInfo skillName={val.skillName} experience={val.experience} />)}
                            </div>
                        </Carousel.Item>
                        <Carousel.Item>
                            <div className="skill-box">
                                {skills?.other.map((val, index) => <SkillInfo skillName={val.skillName} experience={val.experience} />)}
                            </div>
                        </Carousel.Item>
                    </Carousel>
                </div>
            </div>
        </Col>
    )
})

const Skills = (props: Props) => {
    return (
        <Row id={props.id}>
            <Col lg={12}>
                <br /><br /><br /><br />
                <p className="page-title opacity-05" >Skills</p>
                <div className="skill-card">
                    <SkillCard domain="React Native" perc="70" delay={0} />
                    <SkillCard domain="Web development" perc="80" delay={200} />
                    <SkillCard domain="Algorithms" perc="50" delay={400} />
                </div>
            </Col>
            <SkillsConainer />
        </Row>
    )
}

export default React.memo(Skills)
