import React from 'react'
import Container from 'react-bootstrap/Container'
import "./Resume-style.scss"
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/esm/Row'
import { userInfo, EducationProps } from '../../constants/user'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUniversity } from '@fortawesome/free-solid-svg-icons'
import { colors } from '../../style'

interface Props {
    id: string
}

export default function Education(props: Props) {

    const RenderList = ({ startYear, endYear, cource, collage, desc }: EducationProps) => (
        <div className="list-item-container" data-aos="fade-up">
            <div className="info-icon">
                <div className="resume-circle">
                    <FontAwesomeIcon icon={faUniversity} size={"lg"} color={colors.primary_dark} />
                </div>
            </div>
            <div className="info">
                <p className="resume-subTitle" >{startYear + " - " + endYear}</p>
                <p className="resume-heading">{cource}</p>
                <p className="resume-subHeading">{collage}</p>
                <p className="resume-desc">{desc}</p>
            </div>
        </div>
    )

    return (
        <Row id={props.id}>
            <Col lg={12}>
                <br /><br /><br /><br />
                <p className="page-title" >Education</p>
                {userInfo.resume.education?.map((val, index) => <RenderList {...val} />)}
            </Col>
        </Row>
    )
}